.card
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
    background-color: #f5f5f5;
    padding: 15px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.card img
{
    width: 6.9em;
    margin-bottom: 0.1em;
}

.card p:last-child 
{
    margin-top: 0.4rem;
}

.star
{
    color: gold;
}

.card:hover
{
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.9);
}