*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
}

body
{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

.app
{
  max-width: 95%;
  margin: 0 auto;
  padding: 0.4em 5em;
}

@media (max-width: 600px)
{
  .app
  {
    max-width: 100%;
    padding: 0.4em 2em;
  }
}

@media (min-width: 750px)
{
  .app
  {
    max-width: 80%;
  }
}

@media (min-width: 950px)
{
  .app
  {
    max-width: 65%;
  }
}
